import React from 'react';
import {Form, Tag, Modal, Row, Col, Card} from 'antd';

class StocklogView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            key: 'tab1'
        }
    }
    onTabChange = (key) => {
        this.setState({key: key})
    };
    content = (header) => header ? (
        <div>
            <Row>
                <Col span={7} style={{textAlign: "right"}}>
                    <Form.Item></Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item>{header.stockname}</Form.Item>
                </Col>
                <Col span={4} style={{textAlign: "right"}}>
                    <Form.Item></Form.Item>
                </Col>
                <Col span={8}>
                    <Form.Item>{header.category}</Form.Item>
                </Col>
            </Row>
            <Row>
                <Col span={7} style={{textAlign: "right"}}>
                    <Form.Item></Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item>{header.amount}</Form.Item>
                </Col>
                <Col span={4} style={{textAlign: "right"}}>
                    <Form.Item></Form.Item>
                </Col>
                <Col span={8}>
                    <Form.Item>{header.unit}</Form.Item>
                </Col>
            </Row>
            <Row>
                <Col span={7} style={{textAlign: "right"}}>
                    <Form.Item></Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item>
                        {header.amout > header.excess ? <Tag size="small" color="blue"></Tag> :
                            header.state < header.warning ? <Tag size="small" color="red"></Tag> :
                                <Tag size="small" color="green"></Tag>}
                    </Form.Item>
                </Col>
                <Col span={4} style={{textAlign: "right"}}>
                    <Form.Item></Form.Item>
                </Col>
                <Col span={8}>
                    <Form.Item>
                        :{header.warning}&nbsp;&nbsp;
                        :{header.excess}
                    </Form.Item>
                </Col>
            </Row>
            <Row>
                <Col span={7}>
                    <Form.Item style={{textAlign: "right"}}></Form.Item>
                </Col>
                <Col span={17}>
                    {
                        String(header.pictures).split(',').map((item, index) => {
                            return item==="" ? null : <img
                                key={index} src={'http://localhost:3001/upload/'+item} alt=""
                                style={{
                                    width: "80px",
                                    height: "80px",
                                    padding: "5px",
                                    marginRight: "10px",
                                    borderRadius: "5px",
                                    border: "1px solid lightgray"
                                }}
                            />
                        })
                    }
                </Col>
            </Row>
            <Row style={{marginTop: '10px'}}>
                <Col span={7}>
                    <Form.Item style={{textAlign: "right"}}></Form.Item>
                </Col>
                <Col span={17}>
                    <Form.Item>{header.note ? header.note : ''}</Form.Item>
                </Col>
            </Row>
        </div>
    ) : null;
    render() {
        const contentList = {
            tab1: this.content(this.props.info.before),
            tab2: this.content(this.props.info),
        };
        return (
            <Modal
                visible={this.props.visible}
                // title={<span style={{color: "#1DA57A"}}>-{this.props.info.operation || ""}</span>}
                okText=""
                cancelText=""
                onOk={this.props.onOk}
                onCancel={this.props.onCancel}
                footer={null}
            >
                {
                    Object.getOwnPropertyNames(this.props.info).length === 0 ? null :
                        this.props.info.hasOwnProperty('before') ? (
                            <Card
                                style={{width: "100%", border: "none"}}
                                tabList={[{key: 'tab1', tab: ''}, {key: 'tab2', tab: ''}]}
                                activeTabKey={this.state.key}
                                onTabChange={key=>this.onTabChange(key)}
                            >
                                {contentList[this.state.key]}
                            </Card>
                        ) : (
                            <Card
                                style={{width: "100%", border: "none"}}
                                title={<span style={{ color: "#1DA57A", marginLeft: "10px"}}>{this.props.info.operation}</span>}
                            >
                                {this.content(this.props.info)}
                            </Card>
                        )
                }
            </Modal>
        )
    }
}

export default StocklogView;

