import React from 'react';
import {Form, Input, Card, Button, message, Select, Switch} from 'antd';
import TopNav from "../../components/top-nav";
import { updateCategoryList } from "../../api";

const EditCategory = Form.create({ name: 'category-add' })(
    class extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                data: []
            }
        }
        UNSAFE_componentWillMount = () => {
            this.setState({
                data: this.props.location.state.data
            })
        };
        validateName = (rule, value, callback) =>{
            if(!value){
                callback("")
            }else if(value.length>10){
                callback("10")
            }else{
                callback()
            }
        };
        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFields( async (err, values) => {
                if(!err){
                    let data = this.props.location.state.data;
                    data.name = values.name;
                    data.level = values.level;
                    data.isnav = values.isnav === true ? 1 : 0;
                    data.note = values.note;
                    delete data.index;
                    delete data.levelText;
                    const response = await updateCategoryList(data);
                    if(response.status === 0){
                        message.success(",", 2);
                        this.timerID = setTimeout(()=>{
                            this.props.history.push('/product/category')
                        }, 2000);
                    }
                }
            });
        };
        render() {
            const formItemLayout = {
                labelCol: {span: 8},
                wrapperCol: {span: 10}
            };
            const form = this.props.form;
            return (
                <div>
                    <TopNav nav={['', '', '']}/>
                    <Card title={<span style={{ color: "#1DA57A", fontWeight: "bolder", fontSize: "20px"}}></span>}
                          extra={<Button type="primary" onClick={()=>{this.props.history.push('/product/category')}}></Button>}
                          style={{width: "100%", border: "none"}}
                    >
                        <Form {...formItemLayout} style={{marginTop: "40px"}} onSubmit={this.handleSubmit}>
                            <Form.Item label="">
                                {form.getFieldDecorator('name', {
                                    rules: [{required: true, validator: this.validateName }],
                                    initialValue: this.state.data.name
                                })(<Input />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('level', {
                                    rules: [{required: true}],
                                    initialValue: this.state.data.level
                                })(
                                    <Select>
                                        <Select.Option key={0} value={0}></Select.Option>
                                        <Select.Option key={1} value={1}></Select.Option>
                                        <Select.Option key={2} value={2}></Select.Option>
                                        <Select.Option key={3} value={3}></Select.Option>
                                        <Select.Option key={4} value={4}></Select.Option>
                                        <Select.Option key={5} value={5}></Select.Option>
                                        <Select.Option key={6} value={6}></Select.Option>
                                        <Select.Option key={7} value={7}></Select.Option>
                                        <Select.Option key={8} value={8}></Select.Option>
                                        <Select.Option key={9} value={9}></Select.Option>
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('isnav', {
                                    rules: [{required: true}],
                                    valuePropName: "checked",
                                    initialValue: this.state.data.isnav===1
                                })(<Switch />)}
                            </Form.Item>
                            <Form.Item label="">
                                {form.getFieldDecorator('note', {
                                    initialValue: this.state.data.note
                                })(<Input.TextArea rows={4}/>)}
                            </Form.Item>
                            <Form.Item>
                                <Button style={{margin: "50px 0 0 460px"}} type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Card>
                </div>
            )
        }
    },
);

export default EditCategory;
